#announcement-bar-settings {
    max-width: 800px;

    h1 {
        padding-block: 8px;
    }

    .components-base-control:has(textarea) {
        flex-basis: 400px;
    }

    .components-notice {
        &, &__content {
            margin: 0;
        }
    }

    .components-notice-list {
        display: flex;
        flex-direction: column;
        gap: 8px;
        margin-block-end: 16px;
    }

    .components-panel {
        margin-block-end: 8px;
    }
}